.logos{
  margin: auto;
  background-color: rgb(247, 240, 232);
  width:60%;
  height: 605px;
  border-radius: 5px;
  box-shadow: 4px 4px 4px 2px rgba(214, 213, 213, 0.2);
  display: flex;
  align-items: center;
  }
  
.ul-logos {
  list-style: none;
  display: flex;
  flex-flow: row nowrap;
  max-width:270px;
  margin: auto;
  padding: 0 10px;

}

.li-logo {
  flex-basis: 34%;
  padding: 5%; 
  opacity: 0.6;
}

.li-logo img {
  border: none;
}

.li-logo:hover{
  opacity: 1;
}
  
