.box-profile{
  display: block;
  width: 60%;
  margin: auto;
  padding: 3.5%;
  background-color: rgb(247, 240, 232);
  border-radius: 5px;
  box-shadow: 4px 4px 4px 2px rgba(214, 213, 213, 0.2);
}

.wrap-intro{
  display:flex;
}
