.intro-text{
  margin: 2% 7%;
}

.intro-text p{
  /*flex-basis: 80%;*/
  /*color:rgb(231, 157, 18);*/
  color: black;
  font-size: 1.2rem;
  font-weight: 600;
  color: rgb(163, 162, 162);
}

.body-wrap{
  display: flex;
}

.skills-cv{
  display: inline-block;
  text-align: left;
  flex-basis: 42%;
  margin-left: 7%;
}

.badge-row{
  display: flex;
  flex-flow: row wrap;
}

.badge {
  padding: 0.1rem 0.5rem;
  margin: 0.2rem 0.2rem;
  background-color: white;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 5px;
}

.logos-home{
  position: relative;
  list-style: none;
  display: flex;
  flex-flow: row nowrap;
  margin-top: 14%;
  padding-inline-start: 0px;
}

.logo-home{
  flex-basis: 34%;
  justify-content: space-between;
  padding: 4%; 
  opacity: 0.6;
  z-index: 2;
}

.logo-home img{
  border:none;
  width: 2.5rem;
}

.logo-home:hover{
  opacity: 1;
}

.microodles-div{
  width: 23%;
  margin: auto;
}

.microodles{

  border: none;
  margin: -15px 0px 83px -15px;
  opacity: 0.6;
}

.microodles:hover {
  cursor: pointer;
  opacity: 1;  
}

@media only screen and (max-width: 1400px) {
  .box-profile{
    width: 80%;
  }
  .bar-div{
    width: 90%;
  }
  .intro-text{
    margin: 0% 7%;
  }
  .logos-home {
    margin-top: 2rem;
  }
}

@media only screen and (max-width: 700px) {
  .box-profile{
    width: 86%;
  }
  .microodles{
    visibility: hidden;
  }
  .skills-cv{
    display: inline-block;
    flex-basis: 100%;
    margin-top: -1rem;
  }
}

@media only screen and (max-height: 800px) {
  .resume h2{
    visibility: hidden;
  }
  .cv-icon{
    margin-top:-3rem;
    max-width:3rem;  
  }
}

